<template>
  <div id="app">
    <h1>iBatis Dictionary Struct Generator</h1>
	<div class="container">

		<table  width="100%" border="0">
			<tr>
				<td colspan="3">
					<table border="0" width="100%">
						<tr>
							<td width="10%" align="right" nowrap><b>Java Package Name:</b></td>
							<td align="left"><input type="text" v-model="javaPackageName"/></td>
						</tr>
					</table>
				</td>
			</tr>
			<tr>
				<td colspan="3"><br></td>
			</tr>			
			<tr>
				<td align="left"><b>Oracle Structure</b></td>
				<td></td>
				<td align="left"><b>iBatis Dictionary Structure</b></td>
			</tr>
			<tr>
				<td width="38%" align="left">
					<textarea rows="30" v-model="oracleStructureMsg" />
				</td>
				<td>
					<button v-on:click="generateMap" title="Generate the iBatis Dictionary Structure">Generate iBatis Struct</button>
					&nbsp;
					<button v-on:click="clearGeneratedMap" title="Clear the generated iBatis Dictionary Structure">Clear iBatis Struct</button>
				</td>
				<td width="40%" align="right">
					<textarea wrap="off" rows="30"  v-model="ibatisMapMsg" readonly/>
				</td>
			</tr>
			<tr>
				<td colspan="3" align="right" > <button :disabled="this.ibatisMapMsg == ''" v-on:click="exportGeneratedMap" title="Export the generated iBatis Dictionary Structure">Export iBatis Struct</button> </td>
			</tr>
		</table>
	</div>
	</div>
</template>

<script>
export default {
  name: 'App',
  mounted:function() {
		if (!this.$browserDetect.isChrome) {
			//this.$alert("Please use Chrome --> You'll be redirect to Chrome website in few seconds","Unsupported Browser","error");
			//setTimeout(function(){window.location.href="https://www.google.com/chrome/"}, 5000);
		}
  },
  data() {
    return {
	oracleStructureMsg: "",
	ibatisMapMsg: "",
	javaPackageName: "",
	oracleObjectStructTemplate: "<ORACLE_OBJECT NAME=\"#fwo#\" JAVA_PACKAGE=\"#javaPackage#\">",
	oracleObjectStructAttributeWithType: "<ATTRIBUTE NAME=\"#name#\" JAVA_NAME=\"#javaName#\" JAVA_TYPE=\"#javaType#\"/>",
	oracleObjectStructAttribute: "<ATTRIBUTE NAME=\"#name#\" JAVA_NAME=\"#javaName#\"/>",
	mapName: "",
    };
  }, 
  methods: {
	upperCaseFirstLetter : function (string) {
		return string.charAt(0).toUpperCase() + string.slice(1);
	},
	lowerCaseAllWordsExceptFirstLetters : function (string) {
		return string.replace(/\w\S*/g, function (word) {
        return word.charAt(0) + word.slice(1).toLowerCase();
    });
	},	
    generateMap: function () {
	
		if (this.javaPackageName == null || this.javaPackageName.trim().length == 0) {
			this.$alert("Please specify a Java package name","Invalid input","error");
			return;
		}
	
		if (this.oracleStructureMsg == null || this.oracleStructureMsg.trim().length == 0) {
			this.$alert("Insert an Oracle XML Structure","Invalid input","error");
			return;
		}
	
		var textRows = this.oracleStructureMsg.split('\n');
		textRows = textRows.filter(col => col.trim());
		this.clearGeneratedMap;

		this.mapName = textRows[0].replace(/[a-z]/g, '').replace('(','').trim();

		let oracleAttributeNames = [];
		let javaNames = [];
		let javaTypes = [];
		
		var attributeName = "";
		var realAttributeName = "";
		for (var i = 1; i < textRows.length-1; i++) {
			attributeName = textRows[i].trim().replace(/\s\s+/g, ' ').split(' ');
			realAttributeName = attributeName[0].split("_");
			
			if (attributeName.length > 1) {
				oracleAttributeNames[i-1] = attributeName[0].trim();
				if (attributeName[1].trim().replace(",","").toLowerCase() == 'integer')
					javaTypes[i-1] = "java.lang.Integer";
				else
					javaTypes[i-1] = "";
			}

			if (realAttributeName.length == 1) {
				javaNames[i-1] = realAttributeName[0].toLowerCase();
			} else {
				javaNames[i-1] = realAttributeName[0].toLowerCase();
				for (var j =1;j<realAttributeName.length;j++) {
					javaNames[i-1] += this.lowerCaseAllWordsExceptFirstLetters(realAttributeName[j]);
				}
			}
		}
		
		var xmlMapResult = "";

		if (oracleAttributeNames.length && javaTypes.length && javaTypes.length == javaNames.length) {
			
			xmlMapResult = this.oracleObjectStructTemplate.replace("#fwo#",this.mapName).replace("#javaPackage#",this.javaPackageName);
			xmlMapResult += "\n";
			
			for (var k=0;k<oracleAttributeNames.length;k++) {
				
				if (javaTypes[k] == null || javaTypes[k].trim() == "")
					xmlMapResult += this.oracleObjectStructAttribute.replace("#name#",oracleAttributeNames[k]).replace("#javaName#",javaNames[k]);
				else
					xmlMapResult += this.oracleObjectStructAttributeWithType.replace("#name#",oracleAttributeNames[k]).replace("#javaName#",javaNames[k]).replace("#javaType#",javaTypes[k]);
				
				xmlMapResult += "\n";
			}
			xmlMapResult += "</ORACLE_OBJECT>";
		} else {
			this.$alert("The Oracle xml structure to generate the iBatis structure is not corrected","Invalid values","error");
			return;
		}
	
		this.ibatisMapMsg = xmlMapResult;
    },
	clearGeneratedMap: function () {
		this.ibatisMapMsg = "";
	},
	exportGeneratedMap: function() {
		var link = window.document.createElement("a");
		link.setAttribute("href", "data:text/xml;charset=utf-8,%EF%BB%BF" + encodeURI(this.ibatisMapMsg));
		link.setAttribute("download", this.mapName+".xml");
		link.click(); 		
	}
  }
}
</script>

